:root {
  --px: 10; /* Example value, adjust as needed */
  --white: #ffffff;
  --black: #000000;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


/* Typography */
/* Base font size */
html {
  font-size: 16px;
}

.left-container > * {
width:auto;
margin-bottom:2em;
}

.right-container > * {
width:auto;
margin-bottom:1em;
}

.flex-row {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;

}

.flex-row > * {
  margin-right:1em;
}

.app {
  display: flex;
  flex-direction: column;
  padding: 0.8em;
  align-items: center;
  justify-content: center;
  background-color: #f0f0f0;

}

@media (min-width: 768px) {
  .app {
    padding:2em;
    flex-direction: row;
    align-items:flex-start;
  }

  .app > * {
    width: 50%;
  }

  .left-container {
    margin-right: 1em;
  }
}

.add-adding-bottom {
  padding-bottom: 0.5em;
}

/* Custom Classes for Specific Elements */
.experience-list {
  list-style: none; 
  margin-bottom: 1em;
}

.experience-item {
  margin-bottom: 1.5em; 
}

.experience-item li {
  margin-bottom: 0.25em; 
}

.experience-title {
  font-size: 1.2em;
  font-weight: bold;
  color: #333; 
}

.experience-company {
  font-style: italic; 
}

.experience-description {
  margin-top: 0.5em;
  text-align: justify; 
}

.highlighted-info {
  background-color: #faecaf; 
  color: #000000; 
  padding: 1em;
  border: 1px solid #000;
  margin-top: 1em;
  margin-bottom: 1em;
}

.testimonials-paragraph {
  padding-bottom: 0.5em;;
}

.projects-item {
  padding: 1.2em; 
}

.projects-title {
  font-size: 1.2em; 
  font-weight: bold; 
  color: #333; 

}

.projects-description {
  margin-top: 0.5em; 
  text-align: justify;

}


.projects-outcome {
  list-style: inside; 
  color: #666; 

}


.content-container {
padding: 1em;
}

.projectsitems-container {
  margin-bottom: 2.5em;
}

/*Speech Bubble*/

.bubble {
  position: relative;
  display: inline-block;
  margin: 1em;
  text-align: left;
  line-height: 1.3em;
  background-color: #d6f3ff;
  color: #000;
  padding: 1em;
  box-shadow: 0 -4px #d6f3ff, 0 -8px #000, 4px 0 #d6f3ff, 4px -4px #000, 8px 0 #000, 0 4px #d6f3ff, 0 8px #000, -4px 0 #d6f3ff, -4px 4px #000, -8px 0 #000, -4px -4px #000, 4px 4px #000;
  box-sizing: border-box;

}

.bubble span {
  color: #7f00ff;
}

.bubble::after {
  content: "";
  display: block;
  position: absolute;
  box-sizing: border-box;
}
.bubble.shadow {
  box-shadow: 0 -4px #d6f3ff, 0 -8px #000, 4px 0 #d6f3ff, 4px -4px #000, 8px 0 #000, 0 4px #d6f3ff, 0 8px #000, -4px 0 #d6f3ff, -4px 4px #000, -8px 0 #000, -4px -4px #000, 4px 4px #000, 4px 12px rgba(0, 0, 0, 0.1), 12px 4px rgba(0, 0, 0, 0.1), 8px 8px rgba(0, 0, 0, 0.1);
}
.bubble.mini {
  width: 110px;
  font-size: 16px;
  padding: 4px;
  font-family: monospace;
}
.bubble.medium {
  width: 350px;
}
.bubble.large {
  width: 560px;
  font-size: 24px;
  text-align: left;
  text-transform: uppercase;
}
.bubble.grow {
  width: initial;
}
.bubble.top::after {
  height: 4px;
  width: 4px;
  top: -8px;
  left: 32px;
  box-shadow: 0 -4px #000, 0 -8px #000, 0 -12px #000, 0 -16px #000, -4px -12px #000, -8px -8px #000, -12px -4px #000, -4px -4px #d6f3ff, -8px -4px #d6f3ff, -4px -8px #d6f3ff, -4px 0 d6f3ff#d6f3ff, -8px 0 #d6f3ff, -12px 0 #d6f3ff;
}
.bubble.right::after {
  height: 4px;
  width: 4px;
  top: 84px;
  right: -8px;
  background: #d6f3ff;
  box-shadow: 4px -4px #d6f3ff, 4px 0 #d6f3ff, 8px 0 #d6f3ff, 0 -8px #d6f3ff, 4px 4px #000, 8px 4px #000, 12px 4px #000, 16px 4px #000, 12px 0 #000, 8px -4px #000, 4px -8px #000, 0 -4px #fff;
}
.bubble.bottom::after {
  height: 4px;
  width: 4px;
  bottom: -8px;
  left: 32px;
  box-shadow: 0 4px #000, 0 8px #000, 0 12px #000, 0 16px #000, -4px 12px #000, -8px 8px #000, -12px 4px #000, -4px 4px #d6f3ff, -8px 4px #d6f3ff, -4px 8px #d6f3ff, -4px 0 #d6f3ff, -8px 0 #d6f3ff, -12px 0 #d6f3ff;
}
.bubble.left::after {
  height: 4px;
  width: 4px;
  top: 20px;
  left: -8px;
  background: #d6f3ff;
  box-shadow: -4px -4px #d6f3ff, -4px 0 #d6f3ff, -8px 0 #d6f3ff, 0 -8px #d6f3ff, -4px 4px #000, -8px 4px #000, -12px 4px #000, -16px 4px #000, -12px 0 #000, -8px -4px #000, -4px -8px #000, 0 -4px #d6f3ff;
}

.avatar {
  padding-top:1em;
}